$mainPadding: 200px;
$mainPaddingLG: 140px;
$mainPaddingSm: 100px;

.work-item-header {
	@extend %firstSectionPadding;
	padding-bottom: 220px;
	@include lg {
		padding-bottom: 108px; }
	@include sm {
		padding-bottom: 50px; }
	&__text {
		max-width: 510px; } }

.work-item-hero {
	position: relative;
	padding-bottom: 71.112%;
	display: flex;
	align-items: center;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: 50% 0;
	color: $white;
	margin-bottom: 195px;
	@include lg {
		margin-bottom: 95px; }
	@include sm {
		margin-bottom: 43px; }
	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		img {
			width: 100%;
			height: 100%;
			display: block;
			object-fit: cover;
			object-position: center; } }
	&__inner {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%); }
	&__title {
		color: $white;
		@include sm {
			font-size: 18px; } }
	&__text {
		max-width: 510px; } }

.work-item-descr {
	padding-bottom: 165px;
	@include lg {
		padding-bottom: 75px; }
	@include sm {
		padding-bottom: 25px; }
	&__row {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -20px;
		@include lg {
			margin: 0 -10px; }
		@include sm {
			flex-direction: column; } }
	&__col {
		@include flex-size(50%);
		padding: 0 20px 20px;
		@include lg {
			padding: 0 10px 10px; }
		@include md {
			flex: 0 0 auto;
			max-width: none; }
		@include sm {
			padding: 0 10px 40px; }
		&:nth-child(even) {
			padding-left: 108px;
			@include lg {
				padding: 0 10px 10px;
				text-align: right; }
			@include md {
				padding: 0 10px 10px; }
			@include sm {
				padding: 0 10px 40px;
				text-align: left;
				&:last-child {
					padding-bottom: 0; } } }
		p {
			max-width: 460px; } }
	&__content {
		display: inline-block;
		text-align: left; } }

.text-section {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	margin: 0 -10px;
	@include sm {
		text-align: left;
		flex-direction: column;
		margin: 0; }
	p + p {
		margin-top: 10px;
		@include lg {
			margin-top: 6px; }
		@include sm {
			margin-top: 4px; } }
	&__col {
		padding: 0 10px 20px;
		@include flex-size(50%);
		@include sm {
			flex: 0 0 auto;
			max-width: none;
			padding: 10px 0 0; }
		&:nth-child(even) {
			text-align: right;
			padding-right: 40px;
			@include lg {
				padding-right: 20px; }
			@include sm {
				text-align: left;
				padding-right: 10px; } }
		&_wide {
			@include flex-size(100%); } }
	&__content {
		display: inline-block;
		max-width: 540px;
		text-align: left; }
	&__title {
		margin-bottom: 6px;
		@include sm {
			margin-bottom: 0; } } }


.blockquote-section {
	padding-bottom: 200px;
	@include lg {
		padding-bottom: 100px; }
	@include sm {
		padding-bottom: 43px;
		margin-top: 5px; }
	&__inner {
		max-width: 1222px;
		width: 100%;
		margin: 0 auto;
		padding: 0 30px;
		@include lg {
			max-width: 963px; } } }

.work-item {
	&:not(:last-child) {
		padding-bottom: 190px;
		@include lg {
			padding-bottom: 90px; }
		@include sm {
			padding-bottom: 33px; } }
	&__text {
		padding-bottom: 50px;
		@include lg {
			padding-bottom: 10px; }
		@include sm {
			padding-bottom: 15px; } }
	&__image {
		&_border {
			padding: 160px 180px; }
		&_shadow {
			box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); }
		&_light {
			background-color: $workItemLightBg; }
		&_dark {
			background-color: $workItemDarkBg; }
		img {
			max-width: 100%;
			display: block; } }
	&__video-full {
		position: relative;
		padding-bottom: 60%;
		overflow: hidden;
		.video__control {
			@include circle(240);
			@include lg {
				@include circle(120); }
			@include sm {
				@include circle(60); }
			.icon {
				font-size: 216px;
				@include lg {
					font-size: 108px; }
				@include sm {
					font-size: 54px; } } } } }

.full-width-media {
	&__media {
		position: relative;
		padding-bottom: 56.25%;
		@include cover;
		&:not(:last-child) {
			margin-bottom: 20px;
			@include lg {
				margin-bottom: 12px; }
			@include sm {
				margin-bottom: 20px; } } } }

.wide-media {
	&__media {
		position: relative;
		@include cover;
		padding-bottom: 60%; } }

$galleryGutter: 10px;

.gallery {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -#{$galleryGutter};
	@include lg {
		margin: 0 -6px; }
	@include sm {
		margin: 0; }
	&__left,
	&__right {
		@include flex-size(50%);
		padding: 0 $galleryGutter;
		@include lg {
			padding: 0 6px; }
		@include sm {
			@include flex-size(100%);
			padding: 0; } }
	&__left {
		@include sm {
			margin-bottom: 20px; } }
	&__media {
		position: relative;
		@include cover;
		&_lg {
			padding-bottom: 123%;
			@include sm {
				padding-bottom: 121%; } }
		&_sm {
			padding-bottom: calc(61.5% - #{$galleryGutter});
			@include lg {
				padding-bottom: calc(61.5% - 6px);
				@include sm {
					padding-bottom: 60.5%; } } }
		&_full {
			padding-bottom: calc(30.75% - 20px);
			@include sm {
				padding-bottom: 29%; } }
		+ .gallery__media {
			margin-top: 20px;
			@include lg {
				margin-top: 12px; }
			@include sm {
				margin-top: 20px; } }
		.video__control {
			@include circle(120);
			@include lg {
				@include circle(60); }
			.icon {
				font-size: 108px;
				@include lg {
					font-size: 54px; } } } }
	&__full-width {
		@include flex-size(100%);
		padding: 0 $galleryGutter;
		margin-bottom: 20px;
		@include lg {
			margin-bottom: 12px;
			padding: 0 6px; }
		@include sm {
			padding: 0;
			margin-bottom: 20px; } } }

.video {
	@include coverdiv;
	overflow: hidden;
	&.is-playing {
		@include hover {
			.video__control_pause {
				@include fadein; } } }
	video {
		display: block;
		object-fit: cover;
		@include coverdiv; }
	&__control {
		background-color: rgba(0, 0, 0, 0.4);
		@include vcenter;
		@include vertical;
		z-index: 3;
		&_play {
			@include fadein;
			.is-playing & {
				@include fadeout($tr); } }
		&_pause {
			@include fadeout($tr); }
		.icon {
			display: inline-block;
			vertical-align: middle;
			color: $white;
			@include sm {
				font-size: 80px; } } } }
