.touch {
  @extend %firstSectionPadding;
  padding-bottom: 210px;
  @include lg {
    // padding-top: 168px
    padding-bottom: 60px; }
  @include sm {
 }    // padding-top: 94px
  &__header {
    position: relative;
    margin-bottom: 102px;
    @include lg {
      margin-bottom: 46px; }
    @include sm {
      margin-bottom: 22px; } }
  &__sphere {
    position: absolute;
    top: -16px;
    right: 50px;
    animation: touch-sphere 1s 1.4s forwards;
    opacity: 0;
    @include sm {
      display: none; } }
  &__contacts {
    margin-bottom: 236px;
    @include lg {
      margin-bottom: 84px; }
    @include sm {
      margin-bottom: 60px; }
    .contact {
      margin-bottom: 48px;
      @include lg {
        margin-bottom: 38px; }
      @include sm {
        margin-bottom: 28px; } } }
  &__line {
    margin-bottom: 194px;
    @include lg {
      margin-bottom: 60px; }
    @include sm {
      display: none; } }
  // &__title
  //   margin-bottom: 7px
  &__form-header {
    margin-bottom: 44px;
    @include lg {
      margin-bottom: 4px; }
    @include sm {
      margin-bottom: 0; }
    .touch__title {
      @include lg {
        margin-right: -100px; }
      @include sm {
        margin-right: 0; } } } }

@keyframes touch-sphere {
  to {
    opacity: 1; }
  // from
  //   transform: translateX(-300px)
 }  //   transform: translateX(0)


.contact {
  &__title {
    color: $dark;
    margin-bottom: 4px;
    display: block !important;
    @include sm {
      margin-bottom: 0; } }
  &__info {
    color: $greyHigh;
    display: block !important;
    &_social {
      margin-top: 18px; } }
  &__link {
    color: $greyHigh; } }

.form {
  position: relative;
  &__submit {
    margin-top: 80px;
    @include lg {
      margin-top: 60px; }
    @include sm {
      text-align: center; }
    @include xs {
      .btn {
        width: 100%; } } }
  &__success-message {
    @include coverdiv;
    padding-top: 71px;
    background-color: $white;
    z-index: 5;
    @include fadeout(0.8s);
    &.is-active {
      @include fadein; }
    p {
      margin-top: 30px;
      max-width: 400px; } } }

.form-group {
  position: relative;
  padding-top: 47px;
  textarea {
    border: none;
    border-bottom: 1px solid $greyLow;
    height: 52px;
    resize: none;
    box-sizing: border-box;
    display: block;
    overflow: hidden; }
  &__input {
    width: 100%;
    padding-top: 10px;
    padding-bottom: 11px;
    border-bottom: 1px solid $greyLow;
    font-family: $proxima;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.667;
    color: $dark;
    background-color: transparent !important;
    &.error {
      border-color: $red !important; } }
  &__label {
    position: absolute;
    bottom: 17px;
    left: 0;
    color: $greyMid;
    font-family: $proxima;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.428;
    .is-active & {
      bottom: auto;
      top: 32px; } } }

.form-error {
  position: absolute;
  right: 0;
  bottom: 17px;
  color: $red;
  font-family: $proxima;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.428; }

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  -webkit-text-fill-color: $dark !important;
  -webkit-box-shadow: 0 0 0 50px white inset; }
