.about {
  overflow: hidden;
  padding-top: 270px;
  padding-bottom: 190px;
  min-height: 100vh;
  @include lg {
    padding-top: 174px;
    padding-bottom: 110px; }
  @include sm {
    padding-top: 94px;
    padding-bottom: 52px; }
  &__row {
    // align-items: center
    // height: 700px
    align-items: stretch; }
  &__photo-col {
    position: relative;
    text-align: right;
    font-size: 0;
    @include vertical;
    // height: 100%
    // background-color: $red
    @include sm {
      display: none; } }
  &__photo-wrapper {
    display: inline-block;
    vertical-align: middle;
    margin-right: -60px;
    // margin-top: -17px
    padding-left: 114px;
    position: relative;
    @include lg {
      margin-right: 0;
 }      // margin-top: 28px
    @include r(1100) {
      padding-left: 50px; }
    // +md
    //   padding-left: 30px
 }    // overflow: hidden
  &__photo {
    position: relative;
    box-shadow: 0px 39px 69px rgba(77, 73, 122, 0.1);
    // width: 406px
    &:after {
      content: '';
      @include coverdiv;
      background: rgba(0, 71, 255, 0.22); }
    img {
      max-width: 100%;
      display: block; } }
  &__photo-bg {
    position: absolute;
    left: calc(50% + 100px);
    // top: -113px
    top: -93px;
    bottom: -100px;
    width: 1000px;
    background: rgba(152, 149, 162, 0.1);
    z-index: -1;
    @include lg {
      // top: -32px
      top: -60px;
      bottom: -60px;
      left: calc(50% + 60px); } }
  &__text-col {
    padding-top: 25px;
    padding-bottom: 25Wpx;
    @include lg {
      padding-top: 10px;
      padding-bottom: 10px; } }
  &__title {
    margin-left: 0;
    // margin-bottom: 5px
    // +sm
 }    //   margin-bottom: 8px
  &__descr {
    margin-bottom: 52px;
    margin-right: -10px;
    @include lg {
      margin-bottom: 27px; }
    @include sm {
      margin-right: 0;
      margin-bottom: 23px; } }
  &__motto {
    margin-bottom: 130px;
    @include lg {
      margin-bottom: 60px; }
    @include sm {
      margin-bottom: 42px; } } }
